import React from 'react';
import underConst from './../assets/OE60SH0.jpg';
import { makeStyles, Typography, Card } from '@material-ui/core';

const useStyles = makeStyles(() => ({
  root: {
    minHeight: '100vh',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    flexDirection: 'column',
    position: 'relative',
  },
  heading: {
    // position: 'absolute',
    // zIndex: '2',
    textAlign: 'center',
    fontFamily: 'Montserrat',
    fontWeight: 800,
    color: '#FF5959',
    position: 'relative',
    padding: '0.3rem',
    fontSize: '7.5vw!important',
    '&::after': {
      content: '" "',
      width: '160px',
      height: '2px',
      backgroundColor: '#95CD41',
      position: 'absolute',
      margin: '2px auto',
      bottom: 0,
      right: 0,
      left: 0,
    },
  },
  btn: {
    backgroundColor: '#78B64D!important',
    borderColor: '#78B64D!important',
  },
  span: {
    color: '#95CD41',
  },
  subText: {
    // marginBottom: '2rem',
    color: 'white',
    fontFamily: 'Montserrat',
    fontSize: '28px',
    width: ' 100%',
    textAlign: 'center',
    fontWeight: '500',
  },
  carousel: {
    position: 'absolute',
    top: 0,
    left: 0,
    width: '100%',
    height: '90vh',
  },
  gradient: {
    position: 'absolute',
    top: 0,
    left: 0,
    width: '100%',
    height: '100vh',
    // background: 'linear-gradient(to right bottom, #430089, #82ffa1)',
    backgroundColor: '#000000',
    opacity: '0.8',
    zIndex: '1',
  },
  content: {
    position: 'absolute',
    zIndex: '2',
  },
}));

function Register() {
  const classes = useStyles();
  return (
    <div className={classes.root}>
      <img src={underConst} width="300px" height="300px" />
    </div>
  );
}

export default Register;
